import axios from 'axios';

// API  URL
const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:8000/api/v1';

//   axios   
const api = axios.create({
    baseURL: API_BASE_URL,
    headers: {
        'Content-Type': 'application/json',
    },
    //    
    timeout: 10000,
});

//    
api.interceptors.request.use((config) => {
    //    localStorage
    const token = localStorage.getItem('token');

    //   ,    
    if (token) {
        config.headers.Authorization = `Bearer ${token}`;
    } else {
        //   Telegram Mini App 
        const initData = localStorage.getItem('telegram_init_data');
        if (initData) {
            //   Telegram     tma <token>
            config.headers['Authorization'] = "tma " + initData;
            console.log('  Telegram:', initData);
        } else {
            console.warn(' Telegram    localStorage');
        }
    }

    //   
    console.log(' :', {
        url: config.url,
        method: config.method,
        headers: config.headers,
        data: config.data
    });

    return config;
});

//     
api.interceptors.response.use(
    (response) => {
        console.log(' :', response.data);
        return response;
    },
    async (error) => {
        console.error(' :', {
            message: error.message,
            code: error.code,
            response: error.response?.data,
            status: error.response?.status
        });

        if (error.code === 'ERR_NETWORK') {
            console.error('   . ,   .');
        }
        return Promise.reject(error);
    }
);

//     
const retryRequest = async (fn, maxRetries = 3, delay = 1000) => {
    for (let i = 0; i < maxRetries; i++) {
        try {
            return await fn();
        } catch (error) {
            console.log(` ${i + 1}  ${maxRetries}  :`, error.message);
            if (i === maxRetries - 1) throw error;
            await new Promise(resolve => setTimeout(resolve, delay * (i + 1)));
        }
    }
};

// API   
export const authAPI = {
    //   Telegram
    telegramAuth: async () => {
        const initData = localStorage.getItem('telegram_init_data');
        if (!initData) {
            throw new Error('No Telegram init data available');
        }
        try {
            console.log('   Telegram  :', initData);
            const response = await retryRequest(() =>
                api.post('/auth/telegram', { init_data: initData })
            );
            if (response.data.token) {
                localStorage.setItem('token', response.data.token);
                console.log('  ');
            }
            return response.data;
        } catch (error) {
            console.error('   Telegram:', error);
            throw error;
        }
    },

    //  
    verifyAuth: async (token) => {
        return retryRequest(() => api.post('/auth/verify', { token }));
    },

    // 
    logout: async () => {
        const token = localStorage.getItem('token');
        if (token) {
            try {
                await retryRequest(() => api.post('/auth/logout', { token }));
            } finally {
                localStorage.removeItem('token');
            }
        }
    }
};

