import React, { createContext, useState, useEffect, useContext, useCallback } from 'react';
import { authAPI, userAPI } from '../api';

//  
const AuthContext = createContext(null);

//  
export const AuthProvider = ({ children }) => {
    const [user, setUser] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [isInitialized, setIsInitialized] = useState(false);

    //     
    useEffect(() => {
        const initAuth = async () => {
            try {
                setLoading(true);
                setError(null);

                //   
                const token = localStorage.getItem('token');
                if (token) {
                    try {
                        //   
                        await authAPI.verifyAuth(token);
                        //   
                        const userData = await userAPI.getCurrentUser();
                        setUser(userData.data);
                        setIsInitialized(true);
                        setLoading(false);
                        return;
                    } catch (err) {
                        console.log('Token invalid, trying Telegram auth');
                        //   ,  
                        localStorage.removeItem('token');
                    }
                }

                //      ,    Telegram
                const initData = localStorage.getItem('telegram_init_data');
                if (initData) {
                    try {
                        console.log('Attempting Telegram auth...');
                        const authData = await authAPI.telegramAuth();
                        if (authData.token) {
                            localStorage.setItem('token', authData.token);
                            const userData = await userAPI.getCurrentUser();
                            setUser(userData.data);
                            console.log('Telegram auth successful');
                        }
                    } catch (authErr) {
                        console.error('Telegram auth failed:', authErr);
                        setError('   Telegram');
                    }
                } else {
                    console.log('No Telegram init data available');
                }
            } catch (err) {
                console.error('Auth initialization error:', err);
                setError(err.message || ' ');
            } finally {
                setLoading(false);
                setIsInitialized(true);
            }
        };

        initAuth();
    }, []);

    //  
    const logout = useCallback(async () => {
        try {
            await authAPI.logout();
        } catch (err) {
            console.error('Logout error:', err);
        } finally {
            localStorage.removeItem('token');
            setUser(null);
        }
    }, []);

    //   
    const refreshUserData = useCallback(async () => {
        try {
            const userData = await userAPI.getCurrentUser();
            setUser(userData.data);
            return userData.data;
        } catch (err) {
            console.error('Error refreshing user data:', err);
            //   , 
            if (err.response?.status === 401) {
                logout();
            }
            throw err;
        }
    }, [logout]);

    //     Telegram
    const retryTelegramAuth = useCallback(async () => {
        const initData = localStorage.getItem('telegram_init_data');
        if (!initData) {
            throw new Error('No Telegram init data');
        }

        try {
            setLoading(true);
            setError(null);
            
            const authData = await authAPI.telegramAuth();
            if (authData.token) {
                localStorage.setItem('token', authData.token);
                const userData = await userAPI.getCurrentUser();
                setUser(userData.data);
                return userData.data;
            }
        } catch (err) {
            console.error('Telegram auth retry failed:', err);
            setError(' ');
            throw err;
        } finally {
            setLoading(false);
        }
    }, []);

    // ,    Telegram
    const isTelegramWebApp = useCallback(() => {
        return !!(window.Telegram && window.Telegram.WebApp && window.Telegram.WebApp.initData);
    }, []);

    const value = {
        user,
        loading,
        error,
        isInitialized,
        logout,
        refreshUserData,
        retryTelegramAuth,
        isTelegramWebApp,
        isAuthenticated: !!user,
    };

    return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};

//    
export const useAuth = () => {
    const context = useContext(AuthContext);
    if (!context) {
        throw new Error('useAuth must be used within an AuthProvider');
    }
    return context;
};

export default AuthContext;
