import React from 'react';
import styled from 'styled-components';

//   
const Grid = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  margin: 20px 0;
`;

const Row = styled.div`
  display: flex;
  gap: 5px;
`;

const Cell = styled.div`
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
  text-transform: uppercase;
  border: 2px solid #d3d6da;
  background-color: ${props => {
        if (props.state === 2) return '#6aaa64'; //    
        if (props.state === 1) return '#c9b458'; //  ,     
        if (props.state === 0) return '#787c7e'; //    
        return '#ffffff'; //  
    }};
  color: ${props => (props.state !== undefined ? '#ffffff' : '#000000')};
  transition: all 0.2s ease;
  
  @media (max-width: 480px) {
    width: 40px;
    height: 40px;
    font-size: 20px;
  }
`;

//   Wordle
const WordleGrid = ({ attempts, wordLength, maxTries }) => {
    //         
    const renderGrid = () => {
        const rows = [];

        //    
        for (let i = 0; i < maxTries; i++) {
            const attempt = attempts[i] || { word: '', result: [] };
            const cells = [];

            for (let j = 0; j < wordLength; j++) {
                const letter = attempt.word[j] || '';
                const state = attempt.result[j];

                cells.push(
                    <Cell key={`cell-${i}-${j}`} state={state}>
                        {letter}
                    </Cell>
                );
            }

            rows.push(<Row key={`row-${i}`}>{cells}</Row>);
        }

        return rows;
    };

    return <Grid>{renderGrid()}</Grid>;
};

export default WordleGrid; 