import React, { useState, useEffect, useCallback } from 'react';
import { useParams, useNavigate, useLocation } from 'react-router-dom';
import styled from 'styled-components';
import { gameAPI } from '../api';
import { useAuth } from '../context/AuthContext';
import BetModal from '../components/BetModal';

const Container = styled.div`
  max-width: 800px;
  margin: 0 auto;
  padding: 16px;
`;

const GameHeader = styled.div`
  margin-bottom: 24px;
`;

const BackButton = styled.button`
  background: none;
  border: none;
  color: #0077cc;
  font-size: 16px;
  cursor: pointer;
  padding: 0;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
  
  &:hover {
    text-decoration: underline;
  }
`;

const Title = styled.h1`
  font-size: 24px;
  margin: 0 0 8px;
  color: #333;
  display: flex;
  align-items: center;
`;

const Badge = styled.span`
  background-color: ${props => props.color || '#e0e0e0'};
  color: #fff;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: bold;
  margin-left: 8px;
`;

const Description = styled.p`
  font-size: 16px;
  color: #666;
  margin: 8px 0;
`;

const InfoSection = styled.div`
  background-color: #f9f9f9;
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 24px;
`;

const InfoRow = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 8px 0;
  border-bottom: 1px solid #eee;
  
  &:last-child {
    border-bottom: none;
  }
`;

const InfoLabel = styled.span`
  color: #666;
  font-size: 14px;
`;

const InfoValue = styled.span`
  font-weight: bold;
  color: #333;
  font-size: 14px;
`;

const RewardPoolSection = styled.div`
  background-color: #f0f9ff;
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 24px;
  text-align: center;
`;

const RewardPoolAmount = styled.div`
  font-size: 24px;
  font-weight: bold;
  color: #0077cc;
  margin-bottom: 8px;
`;

const RewardPoolLabel = styled.div`
  font-size: 14px;
  color: #666;
`;

const ActionButton = styled.button`
  background-color: #0077cc;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 12px 20px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  width: 100%;
  margin-bottom: 12px;
  transition: background-color 0.2s;
  
  &:hover {
    background-color: #0066b3;
  }
  
  &:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
`;

const SecondaryButton = styled(ActionButton)`
  background-color: #f0f0f0;
  color: #333;
  
  &:hover {
    background-color: #e0e0e0;
  }
`;

const LoadingIndicator = styled.div`
  text-align: center;
  padding: 40px 0;
  color: #666;
`;

const ErrorMessage = styled.div`
  background-color: #ffebee;
  color: #c62828;
  padding: 12px;
  border-radius: 6px;
  margin: 16px 0;
  font-size: 14px;
`;

const PaymentPendingNotice = styled.div`
  background-color: #fff8e1;
  border: 1px solid #ffcc80;
  color: #f57f17;
  padding: 16px;
  border-radius: 8px;
  margin: 16px 0;
  font-size: 14px;
  text-align: center;
`;

const GamePage = () => {
    const { id } = useParams();
    const { user, isAuthenticated, refreshUserData } = useAuth();
    const navigate = useNavigate();
    const location = useLocation();

    const [game, setGame] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [showBetModal, setShowBetModal] = useState(false);
    const [paymentInfo, setPaymentInfo] = useState(null);
    const [paymentPending, setPaymentPending] = useState(location.state?.paymentPending || false);

    //   
    const fetchGameData = useCallback(async () => {
        try {
            setLoading(true);
            setError(null);

            const response = await gameAPI.getGame(id);
            setGame(response.data);
        } catch (err) {
            console.error('Error fetching game:', err);
            setError('     ');
        } finally {
            setLoading(false);
        }
    }, [id]);

    useEffect(() => {
        fetchGameData();
    }, [fetchGameData]);

    //    
    const handleBack = () => {
        navigate('/');
    };

    // /  ( )
    const handleToggleGameStatus = async () => {
        try {
            setLoading(true);

            if (game.status === 'active') {
                await gameAPI.deactivateGame(id);
            } else {
                await gameAPI.activateGame(id);
            }

            //   
            fetchGameData();
        } catch (err) {
            console.error('Error toggling game status:', err);
            setError('    ');
        } finally {
            setLoading(false);
        }
    };

    //   ( )
    const handleDeleteGame = async () => {
        if (!window.confirm(' ,     ?')) {
            return;
        }

        try {
            setLoading(true);
            await gameAPI.deleteGame(id);
            navigate('/');
        } catch (err) {
            console.error('Error deleting game:', err);
            setError('   ');
            setLoading(false);
        }
    };

    //   
    const handleJoinGame = async () => {
        if (!isAuthenticated) {
            setError('     ');
            return;
        }

        //    ,   
        try {
            setLoading(true);
            //       
            await refreshUserData();
            setShowBetModal(true);
        } catch (err) {
            console.error('Error preparing join:', err);
            setError('     ');
        } finally {
            setLoading(false);
        }
    };

    //  
    const handleBetSubmit = async (betAmount, isBlockchainPayment = false) => {
        try {
            setLoading(true);
            setError(null);
            
            //  gameAPI.joinGame  
            const response = await gameAPI.joinGame(id, betAmount);
            setShowBetModal(false);

            //   blockchain 
            if (response.data.payment_required && response.data.payment_info) {
                setPaymentInfo(response.data.payment_info);
                setPaymentPending(true);
                return;
            }

            //    -    
            if (response.data.lobby_id) {
                navigate(`/lobbies/${response.data.lobby_id}`);
            } else if (response.data.id) {
                navigate(`/lobbies/${response.data.id}`);
            }
        } catch (err) {
            console.error('Error joining game:', err);
            setError(err.response?.data?.error || '    ');
            setLoading(false);
        }
    };

    //    
    const getDifficultyColor = (difficulty) => {
        switch (difficulty?.toLowerCase()) {
            case 'easy':
                return '#4caf50';
            case 'medium':
                return '#ff9800';
            case 'hard':
                return '#f44336';
            default:
                return '#9e9e9e';
        }
    };

    //       
    const formatAmount = (amount) => {
        return parseFloat(amount || 0).toFixed(2);
    };

    // ,      
    const isCreator = game && user && game.creator_id === user.telegram_id;

    if (loading && !game) {
        return (
            <Container>
                <LoadingIndicator>   ...</LoadingIndicator>
            </Container>
        );
    }

    if (error && !game) {
        return (
            <Container>
                <BackButton onClick={handleBack}>   </BackButton>
                <ErrorMessage>{error}</ErrorMessage>
            </Container>
        );
    }

    if (!game) {
        return (
            <Container>
                <BackButton onClick={handleBack}>   </BackButton>
                <ErrorMessage>  </ErrorMessage>
            </Container>
        );
    }

    return (
        <Container>
            <BackButton onClick={handleBack}>   </BackButton>

            {error && <ErrorMessage>{error}</ErrorMessage>}

            <GameHeader>
                <Title>
                    {game.title}
                    <Badge color={getDifficultyColor(game.difficulty)}>
                        {game.difficulty}
                    </Badge>
                    {isCreator && (
                        <Badge color="#9c27b0"> </Badge>
                    )}
                </Title>
                <Description>{game.description || ' '}</Description>
            </GameHeader>

            <InfoSection>
                <InfoRow>
                    <InfoLabel> </InfoLabel>
                    <InfoValue>{game.length}</InfoValue>
                </InfoRow>
                <InfoRow>
                    <InfoLabel> </InfoLabel>
                    <InfoValue>{game.max_tries}</InfoValue>
                </InfoRow>
                <InfoRow>
                    <InfoLabel>  </InfoLabel>
                    <InfoValue>{game.time_limit || 5} </InfoValue>
                </InfoRow>
                <InfoRow>
                    <InfoLabel></InfoLabel>
                    <InfoValue>{formatAmount(game.min_bet)} - {formatAmount(game.max_bet)} {game.currency}</InfoValue>
                </InfoRow>
                <InfoRow>
                    <InfoLabel> </InfoLabel>
                    <InfoValue>x{game.reward_multiplier}</InfoValue>
                </InfoRow>
                <InfoRow>
                    <InfoLabel></InfoLabel>
                    <InfoValue>{game.status === 'active' ? '' : ''}</InfoValue>
                </InfoRow>
            </InfoSection>

            <RewardPoolSection>
                <RewardPoolAmount>
                    {game.currency === 'TON'
                        ? formatAmount(game.reward_pool_ton)
                        : formatAmount(game.reward_pool_usdt)} {game.currency}
                </RewardPoolAmount>
                <RewardPoolLabel> </RewardPoolLabel>
            </RewardPoolSection>

            {isCreator ? (
                <>
                    <ActionButton
                        onClick={handleToggleGameStatus}
                        disabled={loading}
                    >
                        {game.status === 'active' ? ' ' : ' '}
                    </ActionButton>
                    <SecondaryButton
                        onClick={handleDeleteGame}
                        disabled={loading}
                    >
                         
                    </SecondaryButton>
                </>
            ) : (
                <ActionButton
                    onClick={handleJoinGame}
                    disabled={loading || game.status !== 'active' || (user && game.creator_id === user.telegram_id)}
                >
                    
                </ActionButton>
            )}

            {showBetModal && (
                <BetModal
                    game={game}
                    onClose={() => setShowBetModal(false)}
                    onSubmit={handleBetSubmit}
                    userBalance={user}
                    paymentInfo={paymentInfo}
                />
            )}

            {paymentPending && (
                <PaymentPendingNotice>
                       ...
                         .
                </PaymentPendingNotice>
            )}
        </Container>
    );
};

export default GamePage; 